@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,600;0,800;1,100;1,300;1,400;1,600;1,800&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: var(--light-app-header);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--light-white);
}

.App-link {
  color: var(--light-link);
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fade-enter {
  opacity: 0.01;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
div.transition-group {
  position: relative;
}
section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}
:root{--light-primary:#7BA61F;--light-secondary:#CBCBCB;--light-third:#1B1714;--light-fourth:#413E3B;--light-error:#AC2E00;--light-success:#708C32;--light-disabled:#B5B5B5;--light-running:#FFE66E54;--light-completed:#B4D7815E;--light-scheduled:#ffffed;--light-canceled:#DFD7D760;--light-white:#FAFCFA;--light-workset:#FEEDE1B0;--light-empty-workset:#FEEDE1B0;--light-mit-completed:#B4D7815E;--light-mit-abandoned:#DFD7D760;--light-note:#C4E3EF60;--light-app-header:#282c34;--light-starred:#fff6ae5e;--light-link:#E1F1F7F0;--dark-primary:#7BA61F;--dark-secondary:#413E3B;--dark-third:#DFDFDF;--dark-fourth:#CBCBCB;--dark-error:#AC2E00;--dark-success:#708C32;--dark-disabled:#B5B5B5;--dark-running:#ededff;--dark-completed:#edffed;--dark-scheduled:#ffffed;--dark-canceled:#ffeeee;--dark-white:#ffffff;--dark-workset:#ffefcd;--dark-empty-workset:#fff6d6}

.color-running{background-color:var(--light-running)}.color-completed{background-color:var(--light-completed)}.color-scheduled{background-color:var(--light-scheduled)}.color-canceled{background-color:var(--light-canceled)}.color-white{background-color:var(--light-white)}.color-link{background-color:var(--light-link)}.color-starred{background-color:var(--light-starred)}.color-workset{background-color:var(--light-workset)}.color-empty-workset{background-color:var(--light-empty-workset)}.color-mit-completed{background-color:var(--light-mit-completed)}.color-mit-abandoned{background-color:var(--light-mit-abandoned)}.color-note{background-color:var(--light-note)}

.day-calendar{position:relative;width:100%}.day-cert-button{top:0;left:0;position:absolute;width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#FFFFFFB0}

.release-modal h3{font-size:18px}.release-modal h4{font-size:12px;color:gray;font-style:italic}.release-modal ul{font-size:12px;-moz-tab-size:4;tab-size:4}

.--landing-background{font-family:'Montserrat', sans-serif;background:center/cover no-repeat url(/static/media/background_tree_blur.a1360668.jpg);display:flex;flex-direction:column;justify-content:flex-start;height:100vh;position:relative;z-index:5;overflow-y:auto}.--landing-background .logo-header{font-size:32px;font-weight:bold}.--landing-background .logo-img{max-width:250px;margin:0 auto}.--landing-background form{margin-top:1em;width:300px;justify-content:center}.--landing-background .form-group{margin-bottom:8px}.--landing-background .navbar-light .navbar-nav{z-index:5}.--landing-background .navbar-light .navbar-nav .nav-link{color:white !important;border:1px solid white;padding:.25em .5em}.--landing-background .navbar-light .navbar-nav .nav-link:hover{color:black !important;background-color:white}.--landing-background .background-mask{position:fixed;width:100%;height:100%;background-color:rgba(0,0,0,0.6);z-index:3}.--landing-main-content{display:flex;flex-direction:column;justify-content:center;align-items:center;color:var(--light-white);z-index:5;margin-top:16px}.--landing-main-header{color:var(--light-white);font-weight:600;margin-bottom:20px}.--landing-secondary-header{font-size:18px;font-weight:500}.--landing-access-code{font-size:14px}.--landing-button.btn-primary{background-color:var(--light-primary);border-color:var(--light-success)}.--landing-button.btn-primary:hover,.--landing-button.btn-primary:active{background-color:var(--light-success);border-color:var(--light-primary)}.--landing-countdown{display:flex;justify-content:center;margin-bottom:4em;z-index:5}.--landing-hours,.--landing-minutes,.--landing-seconds,.--landing-days{display:flex;flex-direction:column;align-items:center;justify-content:center;width:125px;height:125px;margin:0 1rem;font-size:64px;background-color:rgba(65,62,59,0.75);color:white}.--landing-click-link{text-decoration:underline;color:white;cursor:pointer}.--landing-click-link:hover{color:white}

.helppage{font-family:"Montserrat"}.helppage h1{font-size:24px;font-weight:bold;color:#000000;padding-bottom:24px}.helppage h2{font-size:16px;font-weight:bold;padding-bottom:8px}.helppage body{background-color:transparent;text-align:center}.helppage .fixed-col{position:fixed;width:200px}

body {
  overflow: hidden;
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #f5f5f5;
}

